import { FieldValues, UseFormRegister, UseFormSetValue } from 'react-hook-form';

import styles from './FormInput.module.scss';

export interface IFormInputProps {
  labelText: string;
  name: string;
  type?: string;
  formRegister: UseFormRegister<FieldValues>;
  errorMsg?: string;
  isDisabled?: boolean;
  notDisplayError?: boolean;
  changeHandler?: (e: React.FormEvent<HTMLInputElement>) => void;
  setValue?: UseFormSetValue<FieldValues>;
  isReaded?: any;
  additionalCssClassWrapper?: string;
}

const FormInput: React.FC<IFormInputProps> = ({
  labelText,
  name,
  type = 'text',
  formRegister,
  errorMsg,
  isDisabled = false,
  changeHandler,
  notDisplayError,
  setValue,
  isReaded,
  additionalCssClassWrapper = null,
}) => (
  // useEffect((): any => {
  //   if (setValue) {
  //     setValue(name, undefined);
  //   }
  // }, [isReaded, setValue, name]);
  <div
    className={`${styles.formInput__Wrapper} ${
      styles[`${additionalCssClassWrapper}`]
    }`}
  >
    <input
      className={styles.fomrInput__Input}
      {...formRegister(name)}
      disabled={isDisabled}
      id={name}
      onChange={changeHandler}
      type={type}
    />
    <label className={styles.formInput__Label} htmlFor={name}>
      {labelText}
    </label>
    {!notDisplayError && (
      <span className={styles.formInput__Error}>{errorMsg}</span>
    )}
  </div>
);
export default FormInput;
