import { RefObject, useRef } from 'react';

import FormCheckbox, {
  IFormCheckboxProps,
} from 'components/atoms/FormCheckbox/FormCheckbox';
import FormInput, {
  IFormInputProps,
} from 'components/atoms/FormInput/FormInput';

import styles from './RelativeCheckbox.module.scss';

interface IRelativeCheckboxProps {
  CheckboxConfig: IFormCheckboxProps;
  InputConfig: IFormInputProps;
  isReaded: any;
}

const RelativeCheckbox: React.FC<IRelativeCheckboxProps> = ({
  CheckboxConfig,
  InputConfig,
  isReaded,
}) => {
  const errorRef: RefObject<HTMLInputElement> = useRef(null);

  const handleChange = (e: React.FormEvent<HTMLInputElement>): void => {
    if (e.currentTarget.type === 'number') {
      const inputValue: number = parseInt(e.currentTarget.value, 10);
      if (inputValue < 1) e.currentTarget.value = '1';
      if (inputValue > 10) e.currentTarget.value = '10';
    }
  };

  return (
    <div className={styles.relativeCheckbox__Container}>
      <div className={styles.relativeCheckbox__Wrapper}>
        <FormCheckbox {...CheckboxConfig} />
        {isReaded && (
          <FormInput
            notDisplayError
            {...InputConfig}
            additionalCssClassWrapper="formInput__Wrapper--number"
            changeHandler={handleChange}
            isReaded={isReaded}
          />
        )}
      </div>
      <span className={styles.relativeCheckbox__Error} ref={errorRef}>
        {InputConfig.errorMsg}
      </span>
    </div>
  );
};

export default RelativeCheckbox;
