import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';

import { yupResolver } from '@hookform/resolvers/yup';
import Button from 'components/atoms/Button/Button';
import FormInput from 'components/atoms/FormInput/FormInput';
import Header from 'components/atoms/Header/Header';
import RelativeCheckbox from 'components/atoms/RelativeCheckbox/RelativeCheckbox';
import Wrapper from 'components/molecules/Wrapper/Wrapper';
import { IFormData } from 'utils/apiHandlers/books';
import * as yup from 'yup';

import styles from './BookForm.module.scss';

interface IBookFormProps {
  headerText: string;
  submitText: string;
  submitHandler: SubmitHandler<IFormData>;
  formDefaultData: IFormData | null;
}

const validationSchema = yup.object({
  title: yup.string().required('Pole wymagane.'),
  author: yup.string().required('Pole wymagane.'),
  genre: yup.string().required('Pole wymagane.'),
  isReaded: yup.boolean(),
  rating: yup.number().when('isReaded', {
    is: true,
    then: yup
      .number()
      .required('Ocena wymagana od 1 do 10 dla przeczytanej książki')
      .typeError('Ocena od 1 do 10 wymagana dla przeczytanej książki'),
  }),
});

const BookForm: React.FC<IBookFormProps> = ({
  headerText,
  submitText,
  submitHandler,
  formDefaultData,
}) => {
  const defaultData: FieldValues | undefined = formDefaultData
    ? {
        title: formDefaultData.title,
        author: formDefaultData.author,
        genre: formDefaultData.genre,
        id: formDefaultData.id,
        isReaded: formDefaultData.isReaded,
        rating: formDefaultData.rating,
      }
    : undefined;
  console.log('formDefaultData', formDefaultData);
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
    setValue,
    control,
  } = useForm({
    mode: 'all',
    resolver: yupResolver(validationSchema),
    defaultValues: defaultData,
  });
  const isReaded: any = watch('isReaded');
  return (
    <Wrapper additionalCssClass={styles.form__Container}>
      <form
        className={styles.form__Form}
        onSubmit={handleSubmit(submitHandler)}
      >
        <Header priority={2} text={headerText} />
        <FormInput
          errorMsg={errors.title?.message}
          formRegister={register}
          labelText="Tytuł"
          name="title"
        />
        <FormInput
          errorMsg={errors.author?.message}
          formRegister={register}
          labelText="Autor"
          name="author"
        />
        <FormInput
          errorMsg={errors.genre?.message}
          formRegister={register}
          labelText="Gatunek"
          name="genre"
        />
        <RelativeCheckbox
          CheckboxConfig={{
            formRegister: register,
            labelText: 'Przeczytana',
            name: 'isReaded',
          }}
          InputConfig={{
            errorMsg: errors.rating?.message,
            formRegister: register,
            labelText: 'Ocena od 1 do 10',
            name: 'rating',
            type: 'number',
            setValue,
          }}
          isReaded={isReaded}
        />

        <Button
          additionalCssClassModificator="biggerPadding"
          text={submitText}
          type="submit"
        />
      </form>
    </Wrapper>
  );
};

export default BookForm;
