import { FieldValues, UseFormRegister } from 'react-hook-form';

import styles from './FormCheckbox.module.scss';

export interface IFormCheckboxProps {
  labelText: string;
  name: string;
  formRegister: UseFormRegister<FieldValues>;
  errorMsg?: string;
  onClickHandler?: () => void;
}

const FormCheckbox: React.FC<IFormCheckboxProps> = ({
  labelText,
  name,
  formRegister,
}) => (
  <div className={styles.formCheckbox__Container}>
    <div className={styles.formCheckbox__Wrapper}>
      <input
        className={styles.fomrCheckbox__Input}
        {...formRegister(name)}
        id={name}
        type="checkbox"
      />
      <label className={styles.formCheckbox__Label} htmlFor={name}>
        {labelText}
      </label>
    </div>
  </div>
);
export default FormCheckbox;
